const initState = {
    buttons: [
        { value: '', color: 'green' },
        { value: '', color: 'red' },
        { value: '', color: '#a808dd' },
        { value: '', color: 'blue' },
        { value: '', color: '#d5dbff' },
    ],
    initValue: ['', '', '', '', ''],
    targetValue: [],
    process: '',
    nodeMap: [
        { label: '', value: '', status: 'red' },
        { label: '', value: '', status: 'green' },
        { label: '', value: '', status: '#a808dd' },
        { label: '', value: '', status: 'blue' },
        { label: '', value: '', status: '#d5dbff' },
        { label: '', value: '' },
    ]
}

const reducer = (state = initState, action) => {
    switch (action.type) {
        case 'SET_TARGET_VALUE':
            return {
                ...state,
                targetValue: action.detail,
            }
        case 'SET_NODE':
            return {
                ...state,
                nodeMap: action.detail,
            }
        case 'SET_PROCESS':
            return {
                ...state,
                process: action.detail,
            }
        default:
            return state
    }
}

export default reducer



